.menu-wrapper-closed {
    display: none;
}

.menu-wrapper {
    position: fixed;
    z-index: 98;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;

    box-shadow: inset 0px 0px 100px 60px rgba(0,0,0,0.1);
    background-color: rgba(0,0,0,.3);

}

@keyframes menu-slide-in {
    from {
        transform: translate(-50%);
    }
    to {
        transform: translateX(0%);
    }
}

.menu-container {
    position: fixed;
    display: block;
    z-index: 100;
    top: 0;
    left: 0;


    height: 100vh;
    width: 300px;
    background-color: black;
    padding: 15px;
    color: var(--primary);

    animation: menu-slide-in .2s;
}


.btn-wrapper {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    background-color: transparent;
}

.menu-container  .exit-btn {
    background-color: transparent;
    border: none;
    height: auto;
    width: auto;
}

.exit-btn svg {
    font-size: 1.7rem;
}


nav {

}

nav > .mini-navbar {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    list-style: none;
    margin: none !important;
}

.mini-navbar a {
    text-decoration: none;
    color: var(--primary);
    text-align: left;
    font-size: 24px !important;
}

.mini-navbar li:hover,
.mini-navbar button:hover {
    color: rgba(255,255,255,.8) !important;
}

.mini-navbar svg,
.mini-navbar path,
.mini-navbar a {
    margin: 0;
    color: inherit !important;
}
