.aboutme-wrapper {
}

.greeting {
    border-bottom: 1px solid var(--primary);
}
.greeting > h1 {

}
.greeting > .icons {
    font-size: 2.5rem;
    padding-top: .2rem;
}

.icons {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;

}

.icons svg {
    transition: .2s all ease-in-out;
    margin-right: .5rem;
}
.icons svg:hover {
    transform: scale(.95);
}

.aboutme-content {
    width: 100%;
    height: auto; 

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-top: 1.5rem;
    
}


.skill-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    order: 0;
}

.skill {
    height: auto !important;
    background-color: var(--primary);
    color: black;
    border-radius: 3px;
    margin: .3rem;
    font-size: 1rem;
    line-height: auto !important;
    transition: .2s all ease-in-out;
    padding: .5rem .5rem;
}

.skill:hover {
    transform: scale(.95);
    color: rgba(0,0,0,.5);
}

.aboutme-desc {
    align-self: flex-start;
    line-height: 1.5rem;
    font-size: 1.2rem;
    width: 50%;
    
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;

}


.portrait-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: clamp(360px, 50%, 1080px);
    padding: 0 3rem;

    order: 1;
}

.portrait-container {
    max-width: 100%;
    max-height: 100%;
    /**/
}

.portrait-container > img {
    height: 100%;
    width: 100%;
    border-radius: 10px;
}

@media (max-width: 1440px) {

    .portrait-container {
        box-shadow: none;
    }

    .aboutme-content {
        flex-direction: column;
        padding: 0;
    }
    .portrait-wrapper {
        padding-bottom: 1.5rem;
        width: clamp(100px, 40%, 768px);
    }

    .aboutme-desc {
        width: 100%;
        font-size: 1.1rem;
        order: 2;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .skill-container {
        justify-content: center;
        order: 0;
        padding-bottom: 1rem;
    }

}

@media (max-width: 768px) {
    .aboutme-desc {

    }
    .skill {
        font-size: .8rem;
    }
    .skill-container {
        padding: 0;
    }
    .portrait-wrapper {
        min-width: 360px;
    }
}


@media (max-width: 425px) {
    .view {
        background-color: transparent;
        box-shadow:  none;
    }
    .greeting {
        text-align: center;
    }
    .icons {
        justify-content: center;
        width: 100%;
    }
    .aboutme-wrapper {
        padding: 1rem;
    }
    .greeting h1 {
        font-size: 25px;
    }
    .greeting h3 {

        font-size: 12px;
    }
    .description p {
        font-size: 16px;
    }
    .aboutme-contact {
        margin-top: 1rem;
    }
    .aboutme-email {
        margin-top: .3rem;
    }
    body {
        font-size: 16px;
        letter-spacing: 1.5ch;
    }
}