.navbar-wrapper {
    width: 100%;
    height: var(--navBarHeight);

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

    box-shadow: var(--defShadow);

    position: fixed;
    top: 0;
    left:0;
    z-index: 99;

    background-color: rgba(0,0,0,.75);
    backdrop-filter: blur(20px);
}

.nav-title {
    width: auto;
    height: auto;
    text-align: center;
    font-size: 1.7rem;
    margin-right: auto;
}

nav {
    flex-grow: 1;
    width: 100%; 
}

nav > .nav-flex {
    height: 100%;

    margin: 0 15%;

    list-style-type: none;

    display: flex;
    flex: row;
    justify-content: flex-end;
    align-items: center;
}

nav li {
    height: auto;
    border-bottom: 2px solid transparent;
    padding: 0 .5rem;
}

nav button, nav a {
    background-color: transparent;
    border: none;
    font-size: 1rem;
    text-decoration: none;
    display: flex;
    align-items: center;
    height: auto;
    color: inherit;
}
nav svg {
    margin-left: .3rem;
    font-size: 1rem;
    color: inherit;
}
nav path {
    color: inherit;
}
li.unselected-tab:hover {
    /*border-bottom: 2px solid rgba(255,255,255,.5) !important;*/
    color: rgba(255,255,255,.8) !important;
}

.selected-tab {
    /*border-bottom: 2px solid var(--primary);*/
    color: rgba(255,255,255,.5) !important;
}

.nav-icon  {
    display: none;
}

.nav-icon:hover {
    color: rgba(255,255,255,.8);
}

.nav-icon > svg{
    margin: 0;
    color: inherit !important;
}

.nav-icon path {
    color: inherit !important;
}



@media (max-width: 1024px) {
    .nav-icon {
        display: block;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(0, -40%);
    }
    .nav-icon > svg {
        font-size: 1.5rem;
    }
    ul > .selected-tab, ul > .unselected-tab {
        display: none;
    }
    ul > h1.nav-title {
        
        margin-right: 0;
    }
    nav > ul.nav-flex {
        justify-content: center;
        position: relative; 
    }
}

@media (max-width: 768px) {


}

@media (max-width: 425px) {
    nav button, nav a {
        font-size: .8rem;

    }
}