.contactme-wrapper {
    display: flex;
    flex-direction: column;
}

.contactme-header {
    border-bottom: 1px solid var(--primary);
}

.contactme-body {
    margin-top: 2rem;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contactme-body > div {
    flex-grow: 1;
    font-size: 1rem;
    text-align: center;
}

.contactme-body p{
    font-size: 1rem;
}
.contactme-body svg {
    font-size: 2.8rem;
}
.contact-icons {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.space {
    padding-right: 1rem;
}

@media (max-width: 425px) {
    .contactme-body {
        flex-direction: column;
    }

    .contactme-body > * {
        margin: 20px 0;
    }
    .contactme-header > h1 {
        text-align: center;
    }
}