
.projects-wrapper {
    width: 100%;
}

.projects-container {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: stretch;
    flex-wrap: wrap;
}


/*
for one project
*/
.project-wrapper {
    width: clamp(350px, 35%, 720px);
    margin: 0 15px;
    margin-bottom: 2rem;

    border-radius: 5px;
    position: relative;
    z-index: 50;
    background-color: rgba(255,255,255,.25);
    backdrop-filter: blur(5px);
    box-shadow: var(--primary);
    overflow: hidden;
}

.projects-wrapper > h1 {
    border-bottom: 1px solid var(--primary);
    margin-bottom: 2.5rem;
}

.project-wrapper h2 {
    padding: .5rem;
}

.project-wrapper p {
    padding: .5rem;
    padding-top: 0;
}

.stack {
    font-size: 2rem;
    padding: 0 .5rem;
    padding-top: .5rem;

}
.stack > svg {
    margin-right: .8rem;
    position:relative;
}


.project-img-container {
    width: 100%;
    height: auto;
    transition: .3s all ease-in-out;
    position: relative;
}

.project-img-container > img {
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 1;
    cursor: pointer;
    overflow: hidden;
}

.project-img-container:hover {
    transform: scale(.9);
    cursor: pointer;

}

.hidden {
    display: none;
}

.clickme {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    
    display: flex;
    justify-content: center;
    align-items: center;

    color: black;
    font-size: 2rem;
    text-decoration: none;
}


.blur {
    filter: blur(5px);
    border-radius: 5px;
}

@media (max-width: 425px) {
    .projects-wrapper {
        padding: 0 1rem;
        margin-top: 1rem;
    }
    .projects-wrapper h1 {
        font-size:25px;
    }
}