@import url('https://fonts.googleapis.com/css2?family=EB+Garamond&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@200;300&display=swap');

:root {
  --defShadow:rgba(0, 0, 0, 0.35) 0px 5px 15px;
  --primary: #f5f5dc;
  --navBarHeight: 70px;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
/*  font-family: 'EB Garamond', serif;*/
font-family: 'Inter', sans-serif;
  color: var(--primary);
  letter-spacing: .2ch;

}

html, body {
  background-color:#16161d ;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 15%;
  padding-bottom: 2rem;
}

.view {
  height: auto;
  width: 100%;
  padding: 3rem;

  box-shadow: var(--defShadow);
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(5px);
  margin-top: var(--navBarHeight);
  margin-top: calc(var(--navBarHeight) + 50px);
}

.landingpage-wrapper {
}

.arrows-wrapper {
  width: 100%;
  height: auto;
  margin-top: var(--navBarHeight);

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.arrows-wrapper > button {
  margin: 20px 2rem;
  font-size: 50px;
  background-color: transparent;
  border: none;
  height: auto;
  width: auto;
  border-radius: 50%;
}

.arrows-wrapper > button {
}


.arrows-wrapper path,
.arrows-wrapper svg {
  color: inherit;
}


.arrows-wrapper > button:hover {
  color: rgba(255,255,255,.5) !important;
  transform: scale(1.1);
}

.hide-arrow {
  color: rgba(255,255,255,.5);
}

.hide-arrow:hover {
  transform: scale(1) !important;
}